html, body {
  margin: 0;
  padding: 0;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
    justify-content: center;
    column-gap: 30px;
  }
}

.photo:hover {
  transform: scale(1.1);
}

.person {
  transition: all 1s;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.05);
}

.photo {
  transition: all 1s;
  position: relative;
}

.photo__round {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: all 1s;
}

.photo:hover .photo__round {
  border-radius: 60px;
}

.name {
  text-align: center;
  color: #2f4f4f;
  font-family: Arial;
  font-size: 20px;
  line-height: 23px;
}

.details {
  justify-items: center;
}

.rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  background-position: 84px 148px;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-family: Arial;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
  position: absolute;
  top: 84px;
  left: 158px;
}
